/* ------------------------------------------------------------------------------
 *
 *  # Timeline
 *
 *  Styles for timeline in 3 layouts: left, right and centered
 *
 *  Version: 1.1
 *  Latest update: Dec 11, 2015
 *
 * ---------------------------------------------------------------------------- */
.timeline {
  position: relative;
  height: 100%;
}
.timeline:before,
.timeline:after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -2px;
  background-color: #ccc;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  z-index: 1;
}
.timeline:after {
  top: auto;
  bottom: 0;
}
.timeline-container {
  position: relative;
  padding-top: 10px;
  margin-top: -10px;
  padding-bottom: 1px;
}
.timeline-container:after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -1px;
  background-color: #ccc;
  height: 100%;
  width: 2px;
  z-index: -1;
}
.timeline-row {
  position: relative;
}
.timeline-content {
  margin-bottom: 20px;
}
.timeline-date {
  text-align: center;
  background-color: #f5f5f5;
  position: relative;
  z-index: 1;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}
.timeline-date:before,
.timeline-date:after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -2px;
  background-color: #ccc;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  z-index: 1;
}
.timeline-date:after {
  top: auto;
  bottom: 0;
}
.panel .timeline-date {
  background-color: #fff;
}
.timeline-time {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #f5f5f5;
  position: relative;
  margin-bottom: 20px;
}
.timeline-time:before,
.timeline-time:after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -2px;
  background-color: #ccc;
  height: 4px;
  width: 4px;
  border-radius: 50%;
}
.timeline-time:after {
  top: auto;
  bottom: 0;
}
.panel .timeline-time {
  background-color: #fff;
}
.timeline-time span {
