    left: 100%;
    margin-left: -24px;
  }
  .timeline-center .timeline-row.post-even .timeline-icon {
    left: auto;
    right: 100%;
    margin-left: 0;
    margin-right: -24px;
  }
  .timeline-center .timeline-icon:after {
    right: 100%;
    margin-right: 4px;
  }
  .timeline-center .post-even .timeline-icon:after {
    left: 100%;
    margin-left: 4px;
  }
  .timeline-center .post-full .timeline-icon {
